* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-transform: capitalize !important;
    font-family: 'Oswald' !important;
}

* {
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

* {
    -webkit-tap-highlight-color: transparent;
}

button:focus {
    outline: 0;
}

input:focus {
    outline: none;
}

input {
    user-select: all !important;
}

select:focus {
    outline: none;
}

textarea:focus {
    outline: none;
}

a {
    text-decoration: none;
    color: #fff;
}

body {
    overflow-x: hidden;
}

::-webkit-scrollbar {
    width: 8px;
    height: 5px;
}

::-webkit-scrollbar-track {
    border-radius: 0px;
}

::-webkit-scrollbar-thumb {
    background: rgb(155, 155, 155);
    border-radius: 0px;
}

::-webkit-scrollbar-thumb:hover {
    background: rgb(94, 94, 94);
    border-radius: 0px;
}

.emptyTable {
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.emptyTable>img {
    min-width: 100px;
}

.emptyTable>p {
    font-size: 24px;
    color: #23364d;
    margin-left: 20px;
}