.servicesBlock {
    font-size: 40px;
    color: #23364d;
    padding-top: 40px;
    padding-bottom: 30px;
    letter-spacing: 2px;
    width: 90%;
    margin-left: 5%;
    margin-top: 50px;
    font-family: 'OswaldLight' !important;
}

.backgroundTitle {
    position: absolute;
    font-size: 700px;
    font-weight: 800;
    letter-spacing: 30px;
    z-index: 0;
    color: #92a1e2c4;
    top: 0;
    word-break: break-all;
    line-height: 700px;
    text-align: center;
}

.backgroundTitle::selection {
    background: none;
    color: #ddcefdc4;
}

.serviceContainer {
    width: 90%;
    height: auto;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;
    margin-left: 5%;
    overflow-x: hidden;
    overflow-y: hidden;
}

.service {
    width: 32%;
    height: 450px;
    border-radius: 5px;
    margin-top: 40px;
    position: relative;
    background: linear-gradient(135deg, rgb(231, 231, 231) 30%, rgba(255, 255, 255, 0.3));
}

.service:nth-last-of-type(1),
.service:nth-last-of-type(2) {
    width: 48%;
}

.service:hover {
    background-color: #f8f8f8;
    cursor: pointer;
    border-radius: 5px 5px 80px 5px;
}

.service>img {
    width: 30px;
    margin-top: 20px;
    margin-left: 20px;
}

.serviceName {
    font-weight: 600;
    color: #23364d;
    padding-top: 10px;
    padding-left: 24px;
    font-size: 26px;
    letter-spacing: 2px;
    font-family: 'OswaldLight' !important;
    text-transform: uppercase !important;
}

.serviceDescription {
    font-family: 'OswaldLight' !important;
    font-size: 20px;
    font-weight: 300;
    color: #23364d;
    padding: 24px;
    padding-top: 15px;
    letter-spacing: 1px;
    line-height: 30px;
    text-transform: lowercase !important;
}

.learnMoreAboutService {
    position: absolute;
    display: flex;
    align-items: center;
    font-size: 26px;
    font-weight: 400;
    color: #5052d6;
    padding: 24px;
    cursor: pointer;
    bottom: 0px;
    padding-bottom: 20px;
    font-family: 'OswaldLight' !important;
}

.learnMoreAboutService>img {
    width: 28px;
    margin-left: 10px;
    padding-top: 8px;
}

@media (max-width:1000px) {
    .service {
        width: 48%;
        height: 400px;
    }
}

@media (max-width:700px) {
    .service>img {
        width: 35px;
        margin-top: 20px;
        margin-left: 20px;
    }

    .servicesBlock {
        margin-left: 10%;
        width: 80%;
    }

    .serviceContainer {
        width: 80%;
        margin-left: 10%;
    }

    .service {
        width: 100%;
    }

    .service:nth-last-of-type(1),
    .service:nth-last-of-type(2) {
        width: 100%;
    }

    .serviceName {
        padding-top: 15px;
        font-size: 26px;
        letter-spacing: 3px;
    }

    .serviceDescription {
        font-size: 20px;
        font-weight: 400;
        padding: 24px;
        padding-top: 20px;
        letter-spacing: 1px;
        line-height: 25px;
    }

    .learnMoreAboutService {
        font-size: 26px;
        font-weight: 400;
        padding: 24px;
        padding-bottom: 40px;
    }

    .learnMoreAboutService>img {
        width: 35px;
    }
}

@media (max-width:400px) {
    .servicesBlock {
        margin-left: 3%;
        width: 94%;
    }

    .serviceContainer {
        width: 94%;
        margin-left: 3%;
    }

    .serviceDescription {
        font-size: 16px;
    }
}