nav {
    width: 100%;
    height: 90px;
    display: flex;
}

.logo {
    margin-left: 5%;
    height: 55px;
    padding-top: 10px;
}

.logo>img {
    height: 55px;
}

.navList {
    width: auto;
    height: 50px;
    margin-top: 25px;
    margin-left: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.listItem {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    color: #23364d;
    text-transform: lowercase !important;
    letter-spacing: 1px;
    z-index: 99;
    position: relative;
    cursor: pointer;
    opacity: 0.8;
}

.listItem:hover {
    opacity: 1;
}

.serviceDropDownListNavBar {
    margin-top: 5px;
    margin-left: 20px;
    position: relative;
    height: 50px;
    width: 90px;
}

.serviceDropDownTitle {
    width: 90px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    overflow-x: visible;
}

.serviceDropDownTitle>p {
    font-size: 18px;
    color: #23364d;
    text-transform: lowercase !important;
    letter-spacing: 1px;
}

.serviceDropDownTitle>img {
    width: 22px;
    margin-left: 4px;
    margin-top: 3px;
    animation-name: '';
    animation-duration: 0.3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.linkServicesContainer {
    width: 250px;
    height: 0;
    border: 10px none solid;
    border-radius: 5px;
    background-color: #fff;
    position: relative;
    z-index: 9999;
    animation-name: '';
    animation-duration: 0.3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    overflow: hidden;
    box-sizing: content-box;
}

.linkServicesContainer>a:nth-of-type(1) {
    border-radius: 5px 5px 0px 0px;
}

.linkServicesContainer>a:last-of-type {
    border-radius: 0px 0px 5px 5px;
}

.oneServiceContainer {
    font-size: 18px;
    color: #23364d;
    background-color: #fff;
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
    text-transform: lowercase !important;
    font-family: 'OswaldLight' !important;
    padding-left: 10px;
}

.oneServiceContainer:hover {
    background-color: #eee;
}

.normalItemOnNavBar {
    height: 90px;
    font-size: 18px;
    color: #23364d;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
    text-transform: lowercase !important;
    opacity: 0.8;
    padding-top: 5px;
    padding-left: 20px;
}

.normalItemOnNavBar:hover {
    opacity: 1;
}

.linksOnNavBar {
    width: 160px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 5%;
    border: 2px #23364dbe solid;
    border-radius: 5px;
    margin-top: 28px;
    text-transform: uppercase !important;
    cursor: pointer;
    color: #23364d;
    background-color: #fff;
}

@media (max-width:800px) {
    .linksOnNavBar {
        width: 140px;
        font-size: 14px;
    }
}

.linksOnNavBar:hover {
    color: #fff;
    background-color: #23364d;
}

@keyframes serviceDropDownOne {
    from {
        height: 0px;
        border: 2px none solid;
    }
    to {
        height: 320px;
        border: 2px #eee solid;
    }
}

@keyframes serviceDropUpOne {
    from {
        height: 320px;
        border: 2px #eee solid;
    }
    to {
        height: 0px;
        border: 2px none solid;
    }
}

@keyframes serviceDropDownTow {
    from {
        height: 0px;
        border: 2px none solid;
    }
    to {
        height: 80px;
        border: 2px #eee solid;
    }
}

@keyframes serviceDropUpTow {
    from {
        height: 80px;
        border: 2px #eee solid;
    }
    to {
        height: 0px;
        border: 2px none solid;
    }
}

@keyframes chevronAnimationToDown {
    from {
        transform: rotateX(0deg);
    }
    to {
        transform: rotateX(180deg);
    }
}

@keyframes chevronAnimationToUp {
    from {
        transform: rotate(180deg);
    }
    to {
        transform: rotate(0deg);
    }
}

.navListPhoneDevice {
    display: none;
}

@media (max-width:700px) {
    .listItem,
    .linksOnNavBar,
    .navList,
    .normalItemOnNavBar {
        display: none;
    }
    .navListPhoneDevice {
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 5%;
        margin-top: 20px;
    }
    .navListPhoneDevice>img {
        width: 50px;
        height: 50px;
        cursor: pointer;
    }
    .linksPhoneSize {
        display: none;
        width: 250px;
        height: auto;
        border: 2px #eeeeee solid;
        border-radius: 5px;
        background-color: #fff;
        position: absolute;
        top: 30px;
        right: 30px;
        z-index: 99;
    }
    .linkPhone {
        margin-top: 10px;
        margin-bottom: 10px;
        height: 40px;
        font-size: 18px;
        color: #444;
        text-transform: lowercase !important;
        font-family: 'OswaldLight' !important;
        letter-spacing: 1px;
        width: 100%;
        padding-left: 30px;
        display: flex;
        align-items: center;
    }
    .linkPhone:hover {
        color: #000;
    }
}

@media (max-width:500px) {
    .navListPhoneDevice {
        right: 3%;
    }
}

.aboutContainer {
    width: 100%;
    height: 100%;
    background-color: #0000004d;
    position: fixed;
    top: 0;
    z-index: 99999;
    overflow-y: auto;
    overflow-x: hidden;
    display: block;
}

.closeAboutContainer {
    width: 100%;
    height: 40px;
    direction: rtl;
    display: block;
}

.closeAboutContainer>img {
    padding: 10px;
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #ffffffb0;
}

.aboutBody {
    width: 100%;
    height: auto;
    background-color: #fff;
    border-radius: 10px;
}

.titleForAbout {
    width: 90%;
    height: auto;
    margin-left: 5%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    color: #23364d;
    font-size: 30px;
    padding-top: 40px;
    font-family: 'OswaldLight' !important;
}

.aboutCCSE {
    color: #23364d;
    padding-top: 30px;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 80px;
    font-size: 20px;
    font-family: 'OswaldLight' !important;
    line-height: 45px;
    text-align: justify;
    text-indent: 50px;
    hyphens: auto;
}